      text: 'Ми живемо в епоху, коли правда перестала бути опорною категорією суспільного життя. Брехня — не виняток, не збій, а вбудований елемент системи, мастило, на якому обертаються механізми влади, економіки, медіа та навіть культури. Сучасна брехня — це не просто підміна фактів, а методологія: вона подається в обгортці емоційного наративу, підлаштовується під очікування аудиторії та часто виглядає переконливіше за правду. Політичні заяви, економічні звіти, рекламні кампанії, особисті бренди в соцмережах — усе це побудоване на наративі, що легко й непомітно замінює реальність. Ми споживаємо не факти, а історії. Історія має бути надихаючою, логічною, «правильною» — та якщо правда заважає цьому, її замінюють. Не грубо, не відверто, а поступово, непомітно, через акценти, узагальнення, маніпуляції контекстом. Це — брехня нового типу. Брехня стала нормою, бо система потребує стабільності, а правда часто руйнує ілюзії, на яких тримається ця стабільність. Незручні факти замінюються «альтернативними», викриття урівноважуються «зустрічними наративами», а совість оголошується суб`єктивною та тому нерелевантною. У результаті людина опиняється у світі, де складно відрізнити реальність від образу, чесність — від наївності, сумнів — від підривної діяльності. Спротив брехні — це не лише акт чесності, а й форма духовного вибору. Совість у цьому контексті — остання інстанція правди. Але її голос майже не чути за шумом системних наративів. Та все ж, саме від внутрішнього вибору людини залежить, чи житиме брехня в ньому — чи стане він одним із тих, хто не дозволить системі повністю поглинути реальність.',
