        'Lies in the modern system. How falsehood became part of it.',
      secondText:
        "We live in an era when truth has ceased to be a supporting category of public life. Lies are not an exception, not a glitch, but an embedded element of the system—a lubricant on which the mechanisms of power, economy, media, and even culture run. Modern lies are not just a substitution of facts, but a methodology: they are presented wrapped in emotional narrative, tailored to audience expectations, and often seem more convincing than the truth. Political statements, economic reports, advertising campaigns, personal brands on social media—all this is built on narrative that easily and imperceptibly replaces reality. We consume not facts, but stories. A story must be inspiring, logical, 'correct'—and if truth interferes, it is replaced. Not roughly, not blatantly, but gradually, imperceptibly, through accents, generalizations, manipulations of context. This is a new type of lie. Lies have become the norm because the system needs stability, and truth often destroys the illusions that support that stability. Inconvenient facts are replaced by 'alternative' ones, exposures are balanced by 'counter-narratives,' and conscience is declared subjective and therefore irrelevant. As a result, a person finds themselves in a world where it's difficult to distinguish reality from image, honesty from naivety, doubt from sabotage. Resistance to lies is not only an act of honesty but a form of spiritual choice. In this context, conscience is the last instance of truth. But its voice is almost inaudible amid the noise of systemic narratives. Nonetheless, the inner choice of a person determines whether the lie will live in them—or whether they will become one of those who will not allow the system to completely absorb reality.",
